.transformCard {
  display: flex;
  justify-content: center;
  align-items: center;

  .scene {
    width: 1000px;
    display: flex;
    justify-content: space-between;
    perspective: 800px;
    .arrowIcon {
      position: absolute;
      top: 20px;
      left: 20px;
      background: #fff;
      color: #000;
      border-radius: 50%;
    }
    .card {
      position: relative;
      width: 240px;
      height: 300px;
      color: white;
      cursor: pointer;
      transition: 1s ease-in-out;
      transform-style: preserve-3d;

      &_flip {
        transform: rotateY(0.5turn);
      }

      // &:hover {
      //   transform: rotateY(0.5turn);
      // }

      .card__face {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
        transition: 1s ease-in-out;
        // -webkit-box-reflect: below 0
        //   linear-gradient(transparent, transparent, rgba(0, 0, 0, 0.4));

        img {
          width: 240px;
          height: 200px;
          object-fit: cover;
        }

        &__back {
          transform: rotateY(0.5turn);
        }
      }
    }
  }
}
